import React from 'react';
import { TripStateWrapper } from '@/components/dashboard/TripStateWrapper';
import { SupportSection } from '@/components/dashboard/SupportSection';
import { MyBookings } from '@/components/dashboard/MyBookings';

export default function DashboardPage() {
    return (
        <div className="container py-8 space-y-8">
            <div className="flex flex-col gap-2">
                <h1 className="text-3xl font-bold">Dashboard</h1>
                <p className="text-muted-foreground">Manage your trips and get support.</p>
            </div>

            {/* Critical Support Section - Given Priority */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                <div className="lg:col-span-2 space-y-8">
                    {/* Existing Trip State Wrapper - Keeping it as it likely handles "Current Trip" logic which is useful */}
                    <div className="mb-8">
                        <h2 className="text-xl font-semibold mb-4">Current Trip Status</h2>
                        <TripStateWrapper />
                    </div>

                    <MyBookings />
                </div>

                <div className="lg:col-span-1">
                    <div className="sticky top-24">
                        <SupportSection />
                    </div>
                </div>
            </div>
        </div>
    );
}
